@import url("https://fonts.googleapis.com/css?family=Chango");

:root {
  --timeline-arrow-color: rgb(255, 255, 255);
  --timeline-progress: 0%;
  --theme-color: rgb(104, 95, 238);
  --start-section-progress: 0%;
  --background-color: rgb(19, 26, 34);
  --text-color: white;
}

body,
html {
  background-size: 100vw;
  background-color: var(--background-color);
  color: black;
  /*font-family: "Chango", sans-serif;*/
  font-family: Coolvetica_b, Helvetica, sans-serif;
  /*font-family: "Impact", sans-serif;*/
  font-size: 40px;
  padding: 0;
  margin: 0;
  max-width: 100%;
  overflow-x: clip;
  scroll-behavior: smooth;
  transition: background-color 0.5s;
}

* {
  box-sizing: border-box;
}

.show,
.hidden {
  position: relative;
  height: max-content;
}

h2 {
  position: relative;
  font-size: 40px;
  margin: 15px 0px 0px 0px;
}

.burger {
  position: fixed;
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 3;
  transform-style: preserve-3d;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: var(--theme-color);
  transition: all 0.5s;
  margin-top: 5px;
  margin-left: 5px;
}

.burgerBtn {
  position: fixed;
  display: none;
  cursor: pointer;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 35px;
  margin-left: 35px;
  transform: translate(-50%, -50%);
}

.burgerBtn .line {
  width: 42px;
  flex-basis: 3px;
  flex-grow: 0;
  flex-shrink: 0;
  border-radius: 0%;
  background-color: white;
  transition: all 0.5s;
}

.burger .line:nth-child(2) {
  margin-top: 9px;
  margin-bottom: 9px;
}

.burger.active {
  width: 170px;
  height: 170px;
  border-radius: 15%;
}

.burger.active .line:nth-child(1) {
  transform: translateY(12px) rotate(45deg);
  transition: all 0.5s;
}

.burger.active .line:nth-child(2) {
  opacity: 0;
  transition: all 0.5s;
}

.burger.active .line:nth-child(3) {
  transform: translateY(-12px) rotate(-45deg);
  transition: all 0.5s;
}

.burgerContent {
  visibility: hidden;
  transform: translateY(50px);
}

.burger.active .burgerContent {
  visibility: visible;
  pointer-events: initial;
  cursor: pointer;
  transition: all 0.5s 0.5s;
}

.burger p {
  margin: 5px 0px 0px 5px;
}

.burger a {
  color: white;
  text-decoration: none;
  font-family: Coolvetica_b, Helvetica, sans-serif;
  font-size: 30px;
  font-weight: bold;
}

.start {
  height: 100vh;
  color: var(--text-color);
  text-align: center;
  transition: 0.5s all;
}

h1 {
  margin: 10px 0px 0px 0px;
}

.location {
  margin: 5px 0px 0px 0px;
  font-size: 25px;
  color: gray;
  font-weight: bold;
}

.duration {
  margin: 5px 0px 0px 0px;
  font-size: 25px;
  color: gray;
  font-weight: normal;
}

.introSection {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  height: 60vh;
  margin-bottom: 10vh;
}

.introTextContainer {
  flex: 1;
  text-align: left;
  margin-left: 20px;
}

.introTextContainer .mugshot {
  border: 6px solid var(--theme-color);
  border-radius: 50%;
  display: block;
  height: auto; /* maintain aspect ratio*/
  margin-left: calc(100% - 270px);
}

.title {
  margin: 0px 0px 10px 0px;
  --bg-size: 200%;
  --color-one: rgb(37, 89, 244);
  font-size: 70px;
  font-weight: bold;
  background: linear-gradient(
      to right,
      var(--color-one),
      rgb(31, 191, 249),
      rgb(31, 249, 216),
      rgb(31, 249, 129),
      rgb(238, 249, 31),
      rgb(31, 249, 249),
      var(--color-one)
    )
    0 0 / var(--bg-size) 100%;
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  animation: move-bg 10s infinite linear;
}

@keyframes move-bg {
  to {
    background-position: var(--bg-size) 0;
  }
}

ul {
  display: inline-block;
  list-style-type: none; /* Remove bullets */
  padding: 0; /* Remove padding */
  margin: 0; /* Remove margins */
  /*height: 95px;*/
}

.circle {
  position: absolute;
  border-radius: 50%;
  background-color: rgb(50, 68, 90);
  z-index: 1;
}

.start .circle:nth-child(1) {
  --circle-size: 60%;
  width: var(--circle-size);
  /* 1:1 Aspect Ratio */
  padding-top: var(--circle-size);
  transform: translate(-50%, -50%);
}

/*.start .circle:nth-child(2) {
  --circle-size: 40%;
  bottom: calc(var(--start-section-progress) * 0.5);
  right: 5%;
  width: var(--circle-size);
  padding-top: var(--circle-size);
  background-color: var(--theme-color);
  transform: translateY(100%);
}*/

.timeline .circle:nth-child(1) {
  --circle-size: 20rem;
  width: var(--circle-size);
  /* 1:1 Aspect Ratio */
  padding-top: var(--circle-size);
  transform: translate(-50%, -60vh);
}

/*.timeline .circle:nth-child(2) {
  --circle-size: 20%;
  bottom: calc((var(--timeline-progress) + 20%) * 0.7);
  right: 5%;
  width: var(--circle-size);
  padding-top: var(--circle-size);
  background-color: var(--theme-color);
}

.timeline .circle:nth-child(3) {
  --circle-size: 5%;
  bottom: calc((var(--timeline-progress) - 10%) * 0.5);
  left: 15%;
  width: var(--circle-size);
  padding-top: var(--circle-size);
  background-color: var(--theme-color);
}*/

.timeline .circle:nth-child(2) {
  bottom: 0;
  right: 0;
  border-radius: 10rem 10rem 0 0;
  width: 20rem;
  height: 10rem;
  transform: translate(40%, 40%);
}

.navbar {
  position: absolute;
  z-index: 2;
  top: 0;
  right: 0;
  margin-right: 20px;
}

.navbar > li {
  margin-left: 20px;
}

.navbar > li a {
  color: white;
  text-decoration: none;
  font-family: Coolvetica_b, Helvetica, sans-serif;
  font-size: 30px;
  font-weight: bold;
}

.navbar > li a::after {
  content: "";
  display: block;
  margin: auto;
  height: 10px;
  width: 0;
  top: 5px;
  background: black;
  transition: all 0.3s;
}

.navbar > li a:hover::after {
  width: 100%;
  height: 5px;
  background: var(--theme-color);
}

.hideIconRight,
.showIconRight {
  position: fixed;
  top: 50%;
  transform: translateY(-59%);
  right: 25px;
  z-index: 3;
}

.hideIconRight li {
  float: none;
  margin-bottom: 5px;
  transform: translateX(150px);
  transition: transform 0.6s;
}

.showIconRight li {
  float: none;
  margin-bottom: 5px;
  transform: translateX(0px);
  transition: transform 0.6s;
}

.hideIconRight li:nth-child(2),
.showIconRight li:nth-child(2) {
  transition-delay: 0.1s;
}

.hideIconRight li:nth-child(3),
.showIconRight li:nth-child(3) {
  transition-delay: 0.2s;
}

li {
  float: left;
  margin-right: 3px;
  margin-left: 3px;
}

.contactSvg {
  position: relative;
  z-index: 2;
  -webkit-filter: drop-shadow(0px 5px 0px rgba(60, 97, 62, 0.7));
  filter: drop-shadow(0px 5px 0px rgba(60, 97, 62, 0.7));
  transform: translateY(0px);
  transition: all 0.2s ease-in-out;
}

.contactSvg:hover {
  -webkit-filter: drop-shadow(0px 15px 2px rgba(60, 97, 62, 0.7));
  filter: drop-shadow(0px 15px 2px rgba(60, 97, 62, 0.7));
  transform: translateY(-10px);
}

.contactSvg rect {
  filter: invert(0);
  transition: filter 0.2s ease-in-out;
}

.contactSvg:hover rect {
  filter: invert(1);
  cursor: pointer;
}

.contactSvg:hover #linkedin {
  filter: invert(32%) sepia(100%) saturate(884%) hue-rotate(170deg)
    brightness(91%) contrast(103%);
}

.contactSvg:hover #github {
  filter: invert(1);
}

.contactSvg:hover #email {
  filter: invert(40%) sepia(36%) saturate(693%) hue-rotate(71deg)
    brightness(96%) contrast(94%);
  cursor: pointer;
}

.scrollDown::before {
  content: "\2193";
  position: absolute;
  bottom: 10%;
  left: 50%;
  color: var(--timeline-arrow-color);
  transform: translateX(-50%);
  transition: color 2s;
  animation: pulse 3s infinite;
}

@keyframes pulse {
  0% {
    transform: translate(-50%, 0px);
    opacity: 0.4;
  }
  50% {
    transform: translate(-50%, 10px);
    opacity: 0.4;
  }
  100% {
    transform: translate(-50%, 0px);
    opacity: 0.4;
  }
}

.aboutMeContainer {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  /*padding-bottom: 50px;
  margin-bottom: 20vh;*/
  height: max-content;
  min-height: 90vh;
  z-index: 1;
}

.aboutMeChild {
  align-self: flex-start;
  font-size: 25px;
  /*margin-left: 5%;
  margin-right: 5%;
  border: 6px solid rgb(13, 13, 13);*/
}

.aboutMeChild.text {
  margin-left: 15%;
  flex: 0 0 60%;
}

.aboutMeChild.animation {
  left: 50px;
  top: 25px;
  flex: 0 0 40%;
}

.aboutMeChild.hidden {
  opacity: 0;
  transform: translateX(-10%);
  transition: all 1s;
}

.aboutMeChild.show {
  opacity: 1;
  transform: translateX(0%);
  transition: all 1s;
}

.aboutMeChild.show:nth-child(2) {
  transition-delay: 0.5s;
}

h3 {
  font-size: 50px;
}

.aboutMeChild > h3 {
  margin: 20px 0px 0px 0px;
}

.aboutMeChild > h3::after {
  content: ".";
  position: absolute;
  color: var(--theme-color);
}
.aboutMeChild.hidden .skillsIcon {
  position: absolute;
  opacity: 0;
}
.aboutMeChild.show .skillsIcon {
  position: absolute;
  opacity: 0;
  animation: fadeInOut 1.5s linear forwards;
}

@keyframes fadeInOut {
  0% {
    opacity: 0;
    transform: translateY(0) rotate(0deg);
  }
  80% {
    opacity: 0.7;
    transform: translateY(-17vh) rotate(-15deg);
  }
  100% {
    opacity: 0;
    transform: translateY(-20vh) rotate(-20deg);
  }
}

@keyframes fadeInOutFlip {
  0% {
    opacity: 0;
    transform: translateY(0) rotate(0deg);
  }
  80% {
    opacity: 0.7;
    transform: translateY(-17vh) rotate(15deg);
  }
  100% {
    opacity: 0;
    transform: translateY(-20vh) rotate(20deg);
  }
}

.aboutMeChild.show .skillsIcon:nth-child(1) {
  left: calc(70px - 50px);
  animation: fadeInOutFlip 1.7s 2s linear forwards;
}
.aboutMeChild.show .skillsIcon:nth-child(2) {
  left: calc(70px + 40px);
  animation-delay: 2.4s;
}
.aboutMeChild.show .skillsIcon:nth-child(3) {
  left: calc(70px - 0px);
  animation-delay: 2.8s;
}
.aboutMeChild.show .skillsIcon:nth-child(4) {
  left: calc(70px + 20px);
  animation-delay: 3.2s;
  animation-duration: 1.7;
}
.aboutMeChild.show .skillsIcon:nth-child(5) {
  left: calc(70px - 30px);
  animation-delay: 3.6s;
}
.aboutMeChild.show .skillsIcon:nth-child(6) {
  left: calc(70px - 10px);
  animation: fadeInOutFlip 1.3s 4s linear forwards;
}
.aboutMeChild.show .skillsIcon:nth-child(7) {
  left: calc(70px + 40px);
  animation-delay: 4.4s;
}
.aboutMeChild.show .skillsIcon:nth-child(8) {
  left: calc(70px - 50px);
  animation-delay: 4.8s;
  animation-duration: 1.7;
}
.aboutMeChild.show .skillsIcon:nth-child(9) {
  left: calc(70px + 50px);
  animation: fadeInOutFlip 1.3s 5.2s linear forwards;
}
.aboutMeChild.show .skillsIcon:nth-child(10) {
  left: calc(70px + 30px);
  animation-delay: 5.6s;
}
.aboutMeChild.show .skillsIcon:nth-child(11) {
  left: calc(70px - 0px);
  animation-delay: 6s;
  animation-duration: 1.3;
}
.aboutMeChild.show .skillsIcon:nth-child(12) {
  left: calc(70px - 20px);
  animation: fadeInOutFlip 1.7s 6.4s linear forwards;
}

.timelineTitle {
  position: relative;
  z-index: 2;
  color: var(--text-color);
  text-align: center;
  margin-bottom: 5vh;
}

.timelineTitle::after {
  content: ".";
  position: absolute;
  color: var(--theme-color);
}

.timeline {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
}

.timeline::after {
  content: "";
  position: absolute;
  height: var(--timeline-progress);
  width: 6px;
  border-radius: 3px;
  /*background-color: rgb(85, 201, 100);*/
  background: linear-gradient(
    rgba(85, 201, 100, 0.2),
    rgb(85, 201, 100) 80%,
    rgba(85, 201, 100, 0.3)
  );
  /*background-color: blue;
  box-shadow: 0 0 10px blue, 0 0 5px blue, 0 0 5px blue;*/
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -3px;
  z-index: 1;
}

.hidden .ring::after {
  position: absolute;
  z-index: 2;
  opacity: 1;
  transform: scale(0);
  transition: all 0.5s;
}

.show .ring::after {
  position: absolute;
  z-index: 2;
  opacity: 1;
  animation: plopp 0.5s;
}

@keyframes plopp {
  0% {
    transform: scale(0);
  }
  70% {
    transform: scale(1.25);
  }
  100% {
    transform: scale(1);
  }
}

.container {
  position: relative;
  width: 100%;
  height: 100%;
}

.left {
  left: 0;
}

.right {
  left: 50%;
}

/*Arrows*/
.left .content::before {
  content: " ";
  height: 0;
  position: absolute;
  margin-top: 29px;
  width: 0;
  z-index: 1;
  left: 100%;
  border: medium solid white;
  border-width: 10px 0 10px 10px;
  border-color: transparent transparent transparent white;
}

.right .content::before {
  content: " ";
  height: 0;
  position: absolute;
  margin-top: 29px;
  width: 0;
  z-index: 1;
  right: 100%;
  border: medium solid white;
  border-width: 10px 10px 10px 0;
  border-color: transparent white transparent transparent;
}

.ring::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -19px;
  margin-top: 65px;
  background-color: whitesmoke;
  border: 9px solid rgb(85, 201, 100);
  border-radius: 50%;
  z-index: 1;
}

.content {
  /*display: inline-block; to make parent size of child*/
  position: relative;
  z-index: 2;
  padding: 20px 50px;
  margin-left: 55px;
  top: 25px;
  background-color: white;
  width: 40%;
  border-radius: 10px;
}

/*.content::after {
  content: "";
  position: absolute;
  height: 100%;
  width: 10px;
  border-radius: 0px;
  background-color: rgb(104, 95, 238);
  top: 0;
  bottom: 0;
  left: 0;
  margin-left: 0px;
  z-index: 0;
}*/

.hidden .content,
.hidden .right::before,
.hidden .left::before {
  opacity: 0;
  transition: all 0.5s;
  transition-delay: 0.5s;
}

.show .content,
.show .right::before,
.show .left::before {
  opacity: 1;
  transition: all 0.5s;
  transition-delay: 0.5s;
}

.info {
  margin: 5px 0px 10px 0px;
  font-size: 25px;
}

.patch {
  position: absolute;
  right: -30px;
  top: -20px;
  background-color: var(--theme-color);
  color: white;
  font-weight: bold;
  border: 8px solid var(--theme-color);
  border-radius: 10px;
  transform: rotate(-23deg);
}

.hidden .patch {
  opacity: 0;
  transform: rotate(-10deg) scale(1.3);
  transition: all 0.5s;
  transition-delay: 0.5s;
}

.show .patch {
  opacity: 1;
  transform: rotate(15deg) scale(0.7);
  transition: all 0.5s cubic-bezier(0.22, 1, 0.36, 1);
  transition-delay: 1.25s;
}

.footer {
  position: relative;
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30vh;
  min-height: 150px;
  margin-top: 10vh;
  background-color: rgb(27, 27, 27);
  color: white;
  font-size: 25px;
}

.footer .contact {
  flex: 1;
  font-weight: bold;
  margin-left: 10%;
  margin-bottom: 5vh;
}

.footer .contact h4 {
  margin-top: 20px;
  margin-bottom: 20px;
}

.footer .contact p {
  margin: 5px 0 0 0;
  font-weight: normal;
  font-size: 80%;
}

.footer .contact p a {
  color: var(--theme-color);
}

.footer .author {
  flex: 1;
  font-size: 80%;
  margin-left: 10%;
}

.footer .author span {
  font-size: 120%;
  font-weight: bold;
}

.hidden #svgComputerPath {
  stroke-dasharray: 75;
  stroke-dashoffset: 74.9;
}

.show #svgComputerPath {
  stroke-dasharray: 75;
  stroke-dashoffset: 43;
  transition: all 1.5s 1s cubic-bezier(0.69, 0, 0.71, 0.23);
}

.show #svgComputerScreenDot {
  animation: blink 1.5s infinite;
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.hidden #svgComputerScreenLine1,
.hidden #svgComputerScreenLine2,
.hidden #svgComputerScreenLine3,
.hidden #svgComputerScreenLine4 {
  stroke-dasharray: 75;
  stroke-dashoffset: 75;
}

.show #svgComputerScreenLine1 {
  stroke-dasharray: 75;
  animation: write1 6s 4.5s infinite;
}

.show #svgComputerScreenLine2 {
  stroke-dasharray: 68;
  animation: write2 6s 5.3s infinite;
}

.show #svgComputerScreenLine3 {
  stroke-dasharray: 75;
  animation: write3 6s 5.6s infinite;
}

.show #svgComputerScreenLine4 {
  stroke-dasharray: 69;
  animation: write2 6s 5.9s infinite;
}

@keyframes write1 {
  0% {
    opacity: 0;
    stroke-dashoffset: 76;
  }
  20% {
    opacity: 1;
    stroke-dashoffset: 65;
  }
  80% {
    opacity: 1;
    stroke-dashoffset: 65;
  }
  90% {
    opacity: 0;
    stroke-dashoffset: 76;
  }
  100% {
    opacity: 0;
    stroke-dashoffset: 76;
  }
}

@keyframes write2 {
  0% {
    opacity: 0;
    stroke-dashoffset: 76;
  }
  10% {
    opacity: 1;
    stroke-dashoffset: 65;
  }
  75% {
    opacity: 1;
    stroke-dashoffset: 65;
  }
  85% {
    opacity: 0;
    stroke-dashoffset: 76;
  }
  100% {
    opacity: 0;
    stroke-dashoffset: 76;
  }
}

@keyframes write3 {
  0% {
    opacity: 0;
    stroke-dashoffset: 76;
  }
  20% {
    opacity: 1;
    stroke-dashoffset: 65;
  }
  70% {
    opacity: 1;
    stroke-dashoffset: 65;
  }
  82% {
    opacity: 0;
    stroke-dashoffset: 76;
  }
  100% {
    opacity: 0;
    stroke-dashoffset: 76;
  }
}

@media screen and (max-width: 350px) {
  .title {
    font-size: 60px;
  }

  h2 {
    font-size: 35px;
  }

  .footer {
    align-items: center;
    flex-direction: column;
    text-align: center;
  }
}

@media screen and (max-width: 400px) {
  .timeline .circle:nth-child(1) {
    --circle-size: 10rem;
    width: var(--circle-size);
    /* 1:1 Aspect Ratio */
    padding-top: var(--circle-size);
    transform: translate(-50%, -50vh);
  }
}

@media screen and (max-width: 750px) {
  .navbar {
    display: none;
  }

  .burger {
    display: block;
  }

  .burgerBtn {
    display: flex;
  }

  .aboutMeChild.text h3 {
    text-align: center;
  }

  .introSection {
    align-items: center;
    flex-direction: column;
    margin-bottom: 2vh;
  }

  .introTextContainer {
    left: 0;
    text-align: center;
    margin-left: 0;
  }

  .introTextContainer .mugshot {
    position: absolute;
    z-index: 2;
    width: 130px;
    height: 130px;
    right: 0;
    /*display: none;
    flex: 0 0 0%;*/
  }

  .title {
    position: relative;
    z-index: 2;
    left: 0;
    margin-left: 0;
  }

  .scrollDown::before {
    bottom: 15px;
  }

  .aboutMeContainer svg {
    display: none;
  }

  .aboutMeChild.show .skillsIcon {
    display: none;
  }

  .aboutMeChild.text {
    margin-left: 0%;
    margin-right: 5%;
    flex: 0 0 90%;
  }

  .aboutMeChild.animation {
    margin-left: -10%;
    flex: 0 0 0%;
  }

  .hideIconRight,
  .showIconRight {
    display: none;
  }

  .footer .contact {
    margin-left: 5px;
  }

  .footer .author {
    margin-left: 10px;
  }
}

@media screen and (max-width: 830px) {
  .timeline::after {
    left: 25px;
  }

  .ring::after {
    left: 25px;
  }

  .content {
    padding: 20px 20px;
    margin-top: 5%;
    margin-left: 55px;
    width: calc(100% - 60px);
  }

  .right {
    left: 0%;
  }

  .left .content::before {
    left: -9px;
    right: 100%;
    border-width: 10px 10px 10px 0;
    border-color: transparent white transparent transparent;
  }

  .right .content::before {
    left: -9px;
  }
}
